import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import SignIn from './views/auth/SignIn.vue'
import SignUp from './views/auth/SignUp.vue'
import NewRegistering from './views/registering/NewRegistering.vue'
import ChecOut from './views/user/ChecOut.vue'
import AG from '@/store/authGuard'
import nAG from '@/store/notauthGuard'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/sign-in',
      name: 'sign-in',
      component: SignIn,
      beforeEnter: nAG
    },
    {
      path: '/sign-up',
      name: 'sign-up',
      component: SignUp,
      beforeEnter: nAG
    },
    {
      path: '/',
      name: 'registering',
      component: Home,
      beforeEnter: AG
    },
    {
      path: '/addproduct',
      name: 'registering-new',
      component: NewRegistering,
      beforeEnter: AG
    },
    {
      path: '/checout',
      name: 'checout',
      component: ChecOut  
    },
  ]
})
