<template>
  <div class="header">
    <div class="header__topmnu d-flex align-items-center">
      <div class="header__topmnu-logo">
        <a href="/" class>
          <img src="../assets/logo.png" width="30" height="30" alt>
          ЖКХ
        </a>
      </div>
      <div class="header__topmnu-profile ml-auto">
        <a
          class="header__topmnu-profile--avatar dropdown-toggle"
          id="dropdownMenuButton"
          data-toggle="dropdown"
          aria-expanded="false"
        >
          <span class="header__topmnu-profile--avatar-img">
            <img src="../assets/avatar.png" class="img-fluid" alt>
          </span>
        </a>
        <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
          <a class="dropdown-item" href="#">Профиль</a>
          <a class="dropdown-item" href="#">Настройки</a>
          <div class="dropdown-divider"></div>
          <a class="dropdown-item" @click.prevent="onLogout" v-if="isUserLoggedIn">Выйти</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    onLogout() {
      this.$store.dispatch("logoutUser");
      this.$router.push("/");
    }
  },
  computed: {
    isUserLoggedIn() {
      return this.$store.getters.isUserLoggedIn;
    }
  }
};
</script>

<style lang="scss">
.header {
  .dropdown-menu {
    padding: 5px 0px;
    color: #757575;
    font-size: 14px;
    border-radius: 5px;
    margin: 5px 0 0;
    a {
      padding: 10px 10px;
      display: block;
      margin: 5px 0;
    }
  }
  &__topmnu {
    background: #3f51b5;
    min-height: 60px;
    padding: 0 15px;
    &-logo {
      a {
        color: #fff;
        text-decoration: none;
      }
    }
    &-profile {
      &--avatar {
        padding: 14px 7px;
        display: block;
        cursor: pointer;
        &:hover {
          background: #354499;
        }
        &-img {
          display: block;
          width: 30px;
          height: 30px;
          img {
            border-radius: 1000px;
          }
        }
        &:after {
          display: none;
        }
      }
    }
  }
}
</style>