import * as fb from 'firebase'

class User {
    constructor(id, email, username, isAdmin = false) {
        this.id = id
        this.email = email
        this.username = username
        this.isAdmin = isAdmin
    }
}

export default {
    state: {
        user: {},
        isAuth: false
    },
    mutations: {
        setUser(state, payload) {
            state.user = payload
            state.isAuth = true
        },
        unsetUser(state) {
            state.user = {}
            state.isAuth = false
        }
    },
    actions: {
        async registerUser({ commit }, { email, password, username }) {
            commit('clearError')
            commit('setLoading', true)
            try {
                await fb.auth().createUserWithEmailAndPassword(email, password)
                fb.database().ref('users/' + fb.auth().currentUser.uid).set({
                    username: username,
                    email: email,
                    isAdmin: false,
                });
                commit('unsetUser')
                commit('setLoading', false)
            } catch (error) {
                commit('setLoading', false)
                commit('setError', error.message)
                throw error
            }
        },
        async loginUser({ commit }, { email, password }) {
            commit('clearError')
            commit('setLoading', true)
            try {
                await fb.auth().signInWithEmailAndPassword(email, password)
                fb.database().ref('users/' +
                fb.auth().currentUser.uid).on('value', function (snapshot) {
                        if (snapshot.val()) {
                            var obj = snapshot.val();
                            const usernameaa = obj.username;
                            const isAdmin = obj.isAdmin;
                            commit('setUser', new User(fb.auth().currentUser.uid, fb.auth().currentUser.email, usernameaa, isAdmin))
                            commit('setLoading', false)
                        }
                    });
            } catch (error) {
                commit('setLoading', false)
                commit('setError', error.message)
                throw error
            }
        },
        autoLoginUser({ commit }, payload) {
            fb.database().ref('users/' +
            fb.auth().currentUser.uid).on('value', function (snapshot) {
                        if (snapshot.val()) {
                            var obj = snapshot.val();
                            const usernameaa = obj.username;
                            const isAdmin = obj.isAdmin;
                            commit('setUser', new User(payload.uid, payload.email, usernameaa, isAdmin))
                            commit('setLoading', false)
                        }
                    });
                    commit('setUser', new User(payload.uid, payload.email))
        },
        logoutUser({ commit }) {
            fb.auth().signOut()
            commit('unsetUser')
        }
    },
    getters: {
        user(state) {
            return state.user
        },
        isUserLoggedIn(state) {
            return state.isAuth !== false
        },
        isUserNotLoggedIn(state) {
            return state.isAuth === false
        }
    }
}
