<script>
export default {
  data() {
    return {
      loading: true
    };
  },
  mounted() {
    setTimeout(() => {
      this.loading = false;
    }, 2100);
  },
  watch: {
    $route() {
      this.loading = true;
      setTimeout(() => {
        this.loading = false;
      }, 2100);
    }
  }
